'use server';

import { db } from '@/lib/firebase';
import { collection, doc, getDoc, getDocs, setDoc, deleteDoc } from 'firebase/firestore';
import type { WorkoutRoutine } from '@/lib/workouts';
import { type PresetExercise, presetExercises as defaultPresetExercises } from '@/lib/preset-exercises';
import { workouts as presetWorkouts } from '@/lib/workouts';

const getUserId = () => 'user-placeholder';

const getUserDocRef = () => {
    const userId = getUserId();
    return doc(db, 'users', userId);
}

const getWorkoutsCollectionRef = () => {
    const userDocRef = getUserDocRef();
    return collection(userDocRef, 'workouts');
}

const getPresetExercisesCollectionRef = () => {
    const userDocRef = getUserDocRef();
    return collection(userDocRef, 'customPresetExercises');
}

export async function getAllPresetExercises(): Promise<PresetExercise[]> {
    let customPresets: PresetExercise[] = [];
    try {
        const customPresetsSnapshot = await getDocs(getPresetExercisesCollectionRef());
        customPresets = customPresetsSnapshot.docs.map(doc => doc.data() as PresetExercise);
    } catch (error) {
        console.error("Firebase connection failed. This is likely due to missing or incorrect Firebase credentials in src/lib/firebase.ts. Cannot fetch custom preset exercises.", error);
    }

    const combined = [...customPresets, ...defaultPresetExercises];
    const uniquePresets = Array.from(new Map(combined.map(p => [p.name, p])).values());
    
    return uniquePresets.sort((a,b) => a.name.localeCompare(b.name));
}

export async function addPresetExercise(exercise: PresetExercise): Promise<void> {
    const exerciseId = exercise.name.trim().toLowerCase().replace(/\s+/g, '-');
    if (!exerciseId) return;
    const presetDocRef = doc(getPresetExercisesCollectionRef(), exerciseId);
    await setDoc(presetDocRef, exercise);
}

export async function getAllWorkouts(): Promise<WorkoutRoutine[]> {
    let customWorkouts: WorkoutRoutine[] = [];
    let deletedPresetIds: string[] = [];
    let workoutOrder: string[] = [];

    try {
        const userDocSnap = await getDoc(getUserDocRef());
        const userData = userDocSnap.data() as { deletedPresetIds?: string[], workoutOrder?: string[] } | undefined;
        
        deletedPresetIds = userData?.deletedPresetIds || [];
        workoutOrder = userData?.workoutOrder || [];

        const workoutsCollection = getWorkoutsCollectionRef();
        const customWorkoutsSnapshot = await getDocs(workoutsCollection);
        customWorkouts = customWorkoutsSnapshot.docs.map(doc => doc.data() as WorkoutRoutine);
    } catch (error) {
        console.error("Firebase connection failed. This is likely due to missing or incorrect Firebase credentials in src/lib/firebase.ts. Running with default workouts.", error);
        return Object.values(presetWorkouts);
    }
    
    const visiblePresetWorkouts = Object.values(presetWorkouts)
        .filter(workout => !deletedPresetIds.includes(workout.id));
    
    const allRoutines = [...visiblePresetWorkouts, ...customWorkouts];
    
    if (workoutOrder.length > 0) {
        const routineMap = new Map(allRoutines.map(r => [r.id, r]));
        const sortedRoutines = workoutOrder
            .map(id => routineMap.get(id))
            .filter((r): r is WorkoutRoutine => !!r);
        
        const unsortedRoutines = allRoutines.filter(r => !workoutOrder.includes(r.id));
        return [...sortedRoutines, ...unsortedRoutines];
    }
    
    return allRoutines;
}

export async function getWorkout(id: string): Promise<WorkoutRoutine | null> {
    try {
        if (Object.keys(presetWorkouts).includes(id)) {
            const userDocSnap = await getDoc(getUserDocRef());
            const deletedPresetIds = userDocSnap.data()?.deletedPresetIds || [];
            if (deletedPresetIds.includes(id)) {
                return null;
            }
            return presetWorkouts[id];
        }
        
        const workoutDocRef = doc(getWorkoutsCollectionRef(), id);
        const workoutSnap = await getDoc(workoutDocRef);
        return workoutSnap.exists() ? (workoutSnap.data() as WorkoutRoutine) : null;

    } catch (error) {
        console.error("Firebase connection failed. This is likely due to missing or incorrect Firebase credentials in src/lib/firebase.ts. Using local data as a fallback.", error);
        if (presetWorkouts[id]) {
            return presetWorkouts[id];
        }
        return null;
    }
}

export async function createWorkout(workout: WorkoutRoutine): Promise<void> {
    const workoutDocRef = doc(getWorkoutsCollectionRef(), workout.id);
    await setDoc(workoutDocRef, workout);
}

export async function updateWorkout(workout: WorkoutRoutine): Promise<void> {
    if (Object.keys(presetWorkouts).includes(workout.id)) {
    }
    const workoutDocRef = doc(getWorkoutsCollectionRef(), workout.id);
    await setDoc(workoutDocRef, workout, { merge: true });
}

export async function deleteWorkout(id: string): Promise<void> {
    if (presetWorkouts[id]) {
        const userDocRef = getUserDocRef();
        const userDocSnap = await getDoc(userDocRef);
        const userData = userDocSnap.data() || {};
        const deletedPresetIds = userData.deletedPresetIds || [];
        if (!deletedPresetIds.includes(id)) {
            await setDoc(userDocRef, { deletedPresetIds: [...deletedPresetIds, id] }, { merge: true });
        }
    } else {
        const workoutDocRef = doc(getWorkoutsCollectionRef(), id);
        await deleteDoc(workoutDocRef);
    }
}

export async function saveWorkoutOrder(ids: string[]): Promise<void> {
    const userDocRef = getUserDocRef();
    await setDoc(userDocRef, { workoutOrder: ids }, { merge: true });
}
