'use client';

import { useState, useEffect, useRef } from 'react';
import { useForm, useFieldArray } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage, FormDescription } from '@/components/ui/form';
import { PlusCircle, Trash2, Save, Library, GripVertical } from 'lucide-react';
import type { WorkoutRoutine } from '@/lib/workouts';
import { type PresetExercise } from '@/lib/preset-exercises';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from "@/components/ui/command";
import { Checkbox } from '@/components/ui/checkbox';
import { createWorkout, getAllPresetExercises, addPresetExercise } from '@/services/workoutService';
import { useToast } from '@/hooks/use-toast';


const equipmentOptions = [
    { id: 'five_lb_1', label: '5 lbs dumbbell x 1' },
    { id: 'five_lb_2', label: '5 lbs dumbbells x 2' },
    { id: 'ten_lb_1', label: '10 lbs dumbbell x 1' },
    { id: 'ten_lb_2', label: '10 lbs dumbbells x 2' },
    { id: 'ten_lb_plate', label: '10 lbs plate x 1' },
    { id: 'bench', label: 'Bench' },
] as const;


const exerciseSchema = z.object({
  name: z.string().min(1, { message: 'Required' }),
  isPreset: z.boolean().default(false),
  addToLibrary: z.boolean().default(false),
  equipment: z.array(z.string()).optional(),
  equipmentOther: z.string().optional(),
  sets: z.coerce.number().int().min(1, { message: 'Must be > 0' }),
  reps: z.string().min(1, { message: 'Required' }),
  weight: z.string().min(1, { message: 'Required' }),
  instructions: z.string().optional(),
});

const workoutSchema = z.object({
  name: z.string().min(1, { message: 'Workout name is required' }),
  description: z.string().min(1, { message: 'Description is required' }),
  exercises: z.array(exerciseSchema).min(1, { message: 'At least one exercise is required' }),
  cardioName: z.string().optional(),
});

type WorkoutFormData = z.infer<typeof workoutSchema>;

const formatEquipment = (equipment: string[] = [], other: string = ''): string => {
    const parts = equipment.map(id => equipmentOptions.find(opt => opt.id === id)?.label).filter(Boolean);
    if (other) {
        parts.push(other);
    }
    return parts.join(', ');
}

export default function NewWorkoutPage() {
  const router = useRouter();
  const [open, setOpen] = useState(false);
  const [presetExercises, setPresetExercises] = useState<PresetExercise[]>([]);
  const { toast } = useToast();

  const dragItem = useRef<number | null>(null);
  const dragOverItem = useRef<number | null>(null);

  useEffect(() => {
    async function loadPresets() {
        const presets = await getAllPresetExercises();
        setPresetExercises(presets);
    }
    loadPresets();
  }, []);

  const form = useForm<WorkoutFormData>({
    resolver: zodResolver(workoutSchema),
    defaultValues: {
      name: '',
      description: '',
      exercises: [],
      cardioName: '',
    },
    mode: 'onChange',
  });

  const { fields, append, remove, move } = useFieldArray({
    control: form.control,
    name: 'exercises',
  });

  const onSubmit = async (data: WorkoutFormData) => {
    try {
        const exercisesToAddToLibrary = data.exercises
            .filter(ex => !ex.isPreset && ex.addToLibrary)
            .map(ex => addPresetExercise({ name: ex.name, instructions: ex.instructions || '' }));
        
        if (exercisesToAddToLibrary.length > 0) {
            await Promise.all(exercisesToAddToLibrary);
        }

        const newWorkout: WorkoutRoutine = {
          id: `custom-${Date.now()}`,
          name: data.name,
          description: data.description,
          exercises: data.exercises.map(ex => {
            const equipmentString = formatEquipment(ex.equipment, ex.equipmentOther);
            return {
                name: ex.name,
                sets: ex.sets,
                reps: ex.reps,
                weight: isNaN(Number(ex.weight)) ? ex.weight : Number(ex.weight),
                instructions: ex.instructions,
                equipment: equipmentString,
                equipmentGrab: !!equipmentString,
            }
          }),
        };
        
        if (data.cardioName) {
            newWorkout.cardio = { name: data.cardioName, duration: '' }
        }

        await createWorkout(newWorkout);

        router.push('/');
    } catch (error) {
        console.error("Failed to save workout:", error);
        toast({
            title: "Save Failed",
            description: "Could not save workout. This can happen if you are offline or your Firebase configuration is not set up correctly.",
            variant: "destructive",
        });
    }
  };

  const addPresetExerciseToForm = (exercise: PresetExercise) => {
    append({
        name: exercise.name,
        instructions: exercise.instructions,
        isPreset: true,
        addToLibrary: false,
        equipment: [],
        equipmentOther: '',
        sets: 1,
        reps: '',
        weight: '',
    });
    setOpen(false);
  }

  const addCustomExercise = () => {
    append({
        name: '',
        instructions: '',
        isPreset: false,
        addToLibrary: false,
        equipment: [],
        equipmentOther: '',
        sets: 1,
        reps: '',
        weight: '',
    });
  }

  const handleDragStart = (e: React.DragEvent<HTMLDivElement>, index: number) => {
    dragItem.current = index;
  };

  const handleDragEnter = (e: React.DragEvent<HTMLDivElement>, index: number) => {
      dragOverItem.current = index;
  };

  const handleDragEnd = () => {
      if (dragItem.current !== null && dragOverItem.current !== null && dragItem.current !== dragOverItem.current) {
          move(dragItem.current, dragOverItem.current);
      }
      dragItem.current = null;
      dragOverItem.current = null;
  };

  return (
    <main className="container mx-auto flex min-h-screen flex-col items-center p-4 sm:p-6">
      <Card className="w-full max-w-3xl">
        <CardHeader>
          <CardTitle className="text-3xl font-headline">Create a New Workout</CardTitle>
          <CardDescription>Build your custom workout routine below.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
              <div className="space-y-4">
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Workout Name</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., Upper Body Strength" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="description"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Description</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., Chest, Shoulders, and Triceps" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <div className="space-y-6">
                 {fields.length > 0 && <h3 className="text-xl font-semibold">Exercises</h3>}
                {fields.map((field, index) => (
                  <div 
                    key={field.id} 
                    className="rounded-lg border p-4 pl-8 space-y-4 relative bg-card cursor-move"
                    draggable
                    onDragStart={(e) => handleDragStart(e, index)}
                    onDragEnter={(e) => handleDragEnter(e, index)}
                    onDragEnd={handleDragEnd}
                    onDragOver={(e) => e.preventDefault()}
                  >
                     <div className="absolute top-1/2 -translate-y-1/2 left-1.5 text-muted-foreground/50 hover:text-muted-foreground">
                        <GripVertical className="h-6 w-6" />
                     </div>
                     <div className="flex justify-between items-center mb-4">
                        <h3 className="font-semibold text-lg">
                           {form.getValues(`exercises.${index}.isPreset`) ? form.getValues(`exercises.${index}.name`) : `Custom Exercise ${index + 1}`}
                        </h3>
                        <Button type="button" variant="ghost" size="icon" className="text-destructive hover:bg-destructive/10" onClick={() => remove(index)}>
                          <Trash2 className="h-4 w-4" />
                          <span className="sr-only">Remove Exercise</span>
                        </Button>
                     </div>
                     
                      {!form.getValues(`exercises.${index}.isPreset`) && <>
                        <FormField
                            control={form.control}
                            name={`exercises.${index}.name`}
                            render={({ field }) => (
                            <FormItem>
                                <FormLabel>Exercise Name</FormLabel>
                                <FormControl>
                                    <Input placeholder="e.g., Bench Press" {...field} />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                            )}
                        />
                         <FormField
                            control={form.control}
                            name={`exercises.${index}.addToLibrary`}
                            render={({ field }) => (
                                <FormItem className="flex flex-row items-start space-x-3 space-y-0 pt-2">
                                    <FormControl>
                                        <Checkbox
                                            checked={field.value}
                                            onCheckedChange={field.onChange}
                                        />
                                    </FormControl>
                                    <div className="space-y-1 leading-none">
                                        <FormLabel>Add to exercise library</FormLabel>
                                        <FormDescription>
                                            Save this exercise for future use in other workouts.
                                        </FormDescription>
                                    </div>
                                </FormItem>
                            )}
                        />
                      </>}

                      <FormField
                        control={form.control}
                        name={`exercises.${index}.equipment`}
                        render={() => (
                            <FormItem>
                                <div className="mb-2">
                                    <FormLabel>Equipment Needed (for reminder)</FormLabel>
                                </div>
                                <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                                {equipmentOptions.map((item) => (
                                    <FormField
                                        key={item.id}
                                        control={form.control}
                                        name={`exercises.${index}.equipment`}
                                        render={({ field }) => {
                                            return (
                                                <FormItem
                                                    key={item.id}
                                                    className="flex flex-row items-start space-x-3 space-y-0"
                                                >
                                                    <FormControl>
                                                        <Checkbox
                                                            checked={field.value?.includes(item.id)}
                                                            onCheckedChange={(checked) => {
                                                                return checked
                                                                    ? field.onChange([...(field.value || []), item.id])
                                                                    : field.onChange(
                                                                        field.value?.filter(
                                                                            (value) => value !== item.id
                                                                        )
                                                                    )
                                                            }}
                                                        />
                                                    </FormControl>
                                                    <FormLabel className="font-normal">
                                                        {item.label}
                                                    </FormLabel>
                                                </FormItem>
                                            )
                                        }}
                                    />
                                ))}
                                </div>
                                <FormField
                                    control={form.control}
                                    name={`exercises.${index}.equipmentOther`}
                                    render={({ field }) => (
                                        <FormItem className="flex flex-row items-center space-x-3 space-y-0 mt-3">
                                            <FormLabel className="font-normal whitespace-nowrap pt-2">Other:</FormLabel>
                                            <FormControl><Input placeholder="e.g., Kettlebell" {...field} /></FormControl>
                                        </FormItem>
                                    )}
                                />
                                <FormMessage />
                            </FormItem>
                        )}
                      />

                     <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                         <FormField
                          control={form.control}
                          name={`exercises.${index}.sets`}
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Sets</FormLabel>
                              <FormControl><Input type="number" placeholder="3" {...field} /></FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                         <FormField
                          control={form.control}
                          name={`exercises.${index}.reps`}
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Reps</FormLabel>
                              <FormControl><Input placeholder="8-12" {...field} /></FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                         <FormField
                          control={form.control}
                          name={`exercises.${index}.weight`}
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Weight</FormLabel>
                              <FormControl><Input placeholder="135 lbs or Bodyweight" {...field} /></FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                     </div>
                      <FormField
                          control={form.control}
                          name={`exercises.${index}.instructions`}
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Reminder (Optional)</FormLabel>
                              <FormControl><Textarea placeholder="e.g. Keep elbows tucked" {...field} /></FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                  </div>
                ))}
                
                <div className="flex flex-col sm:flex-row gap-4">
                    <Dialog open={open} onOpenChange={setOpen}>
                        <DialogTrigger asChild>
                            <Button type="button" variant="outline" className="flex-1">
                                <Library className="mr-2 h-4 w-4" />
                                Add from Library
                            </Button>
                        </DialogTrigger>
                        <DialogContent className="sm:max-w-[425px]">
                            <DialogHeader>
                                <DialogTitle>Select an Exercise</DialogTitle>
                                <DialogDescription>
                                    Search for a preset exercise to add to your routine.
                                </DialogDescription>
                            </DialogHeader>
                            <Command>
                                <CommandInput placeholder="Search exercises..." />
                                <CommandList>
                                    <CommandEmpty>No results found.</CommandEmpty>
                                    <CommandGroup>
                                        {presetExercises.map((exercise) => (
                                            <CommandItem
                                                key={exercise.name}
                                                value={exercise.name}
                                                onSelect={() => addPresetExerciseToForm(exercise)}
                                            >
                                                {exercise.name}
                                            </CommandItem>
                                        ))}
                                    </CommandGroup>
                                </CommandList>
                            </Command>
                        </DialogContent>
                    </Dialog>
                    <Button
                        type="button"
                        variant="outline"
                        onClick={addCustomExercise}
                        className="flex-1"
                    >
                      <PlusCircle className="mr-2 h-4 w-4" />
                      Add Custom Exercise
                    </Button>
                </div>
              </div>

              <div>
                <h3 className="font-semibold mb-2">Final Cardio (Optional)</h3>
                <FormField
                  control={form.control}
                  name="cardioName"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Cardio Name</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., 15-min Incline Walk" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <Button type="submit" size="lg" className="w-full">
                <Save className="mr-2 h-4 w-4" />
                Save Workout
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>
    </main>
  );
}
